





















































































































































import {
  Component,
  Prop,
  Vue,
  Watch,
} from 'vue-property-decorator';
import Modal from '@/components/Modal.vue';

@Component({
  name: 'Farm',
  components: {
    Modal,
  },
})
export default class Farm extends Vue {
  @Watch('dialog') onDialogChanged(val: boolean, oldVal: boolean) {
    console.log('dialog2', val, oldVal);
  }

  private dialog = false;
  private options: object = [
    {
      name: '상추',
      code: '0001',
    },
    {
      name: '배추',
      code: '0002',
    },
    {
      name: '깻잎',
      code: '0003',
    },
    {
      name: '씀바귀',
      code: '0004',
    },
  ];

  private icons: object = [
    'fas fa-lightbulb',
    'fas fa-wind',
    'fas fa-thermometer-quarter',
    'fas fa-door-closed',
  ];

  private power = 20;

  get GetOptions(): object {
    return this.options;
  }
}
