





















import {
  Component,
  Prop,
  PropSync,
  Watch,
  Emit,
  Vue,
} from 'vue-property-decorator';

@Component({
  name: 'Modal',
})

export default class Modal extends Vue {
  @Prop({ default: false }) value!: boolean;
  @Prop({ default: '600px' }) width?: string;
  @Prop({ default: '600px' }) height?: string;
  @Prop({ default: '100%' }) maxWidth?: string;
  @Prop({ default: '100%' }) maxHeight?: string;
  @Prop({ default: '' }) minWidth?: string;
  @Prop({ default: '' }) minHeight?: string;

  @Watch('value') onValueChanged(val: boolean, oldVal: boolean) {
    this.dialog = val;
  }

  private dialog = false;

  created() {
    console.log('modal created');
  }

  @Emit() input() {
    return this.dialog;
  }
}
