import Vue from 'vue';
import VueRouter, { RouteConfig } from 'vue-router';
import {
  NotFound,
  Login,
  Farm,
  Main,
  Setting,
  Detail,
} from './routes';

Vue.use(VueRouter);

const routes: Array<RouteConfig> = [
  {
    path: '/',
    component: Main,
    children: [
      {
        path: '',
        component: Farm,
      },
      {
        path: 'setting/:farm',
        component: Setting,
      },
      {
        path: 'detail/:farm',
        component: Detail,
      },
    ],
  },
  {
    path: 'login',
    component: Login,
  },
  {
    path: '404',
    component: NotFound,
  },
  {
    path: '*',
    redirect: '404',
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;
