





















import { EventListener } from '@amcharts/amcharts4/core';
import {
  Component,
  Prop,
  Emit,
  Model,
  Vue,
  Watch,
} from 'vue-property-decorator';

@Component({
  name: 'Switchs',
})
export default class Switchs extends Vue {
  @Prop() readonly id?: string;
  @Model('change', { type: Boolean }) readonly checked!: boolean;

  created() {
    console.log('switchs created');
  }

  @Emit() change($e: { target: HTMLInputElement }) {
    return $e.target.checked;
  }
}
